<template>
  <div class="wrap mod_address">
    <div class="item flex-center-y">
      <div class="title"></div>
      <input
        type="text"
        placeholder=""
        readonly
        :value="data.province + ' ' + data.city + ' ' + data.district"
        @click="show=true"
      />
      <van-icon name="arrow" class="arrow" />
    </div>
    <div class="item flex-center-y">
      <div class="title"></div>
      <input type="text" placeholder="165501" v-model="data.address" />
    </div>
    <div class="item flex-center-y">
      <div class="title"></div>
      <input type="text" placeholder="" v-model="data.name" />
    </div>
    <div class="item flex-center-y">
      <van-radio-group v-model="data.famale" class="flex">
        <van-radio name="1" checked-color="#EF2C10"></van-radio>
        <van-radio name="2" class="radio" checked-color="#EF2C10"></van-radio>
      </van-radio-group>
    </div>
    <div class="item flex-center-y">
      <div class="title"></div>
      <input type="number" placeholder="" v-model="data.mobile" />
    </div>
    <div id="dizhi" @click="default_()">
      <span class="radio" :class="{active:data.is_default==1}"></span>
      <span></span>
    </div>
    <div class="button flex-center" @click="sub()">{{this.$route.query.data?"":""}}</div>
    <van-popup v-model="show" position="bottom">
      <van-area :area-list="site" @cancel="show=false" @confirm="changeAddress" />
    </van-popup>
  </div>
</template>

<script>
import {
  Icon,
  RadioGroup,
  Radio,
  Checkbox,
  Area,
  Popup,
  Toast,
  NavBar
} from "vant";
import axios from "axios";
import util from "../utils/utils.js";
import url from "@/axios/api.js";
export default {
  components: {
    "van-icon": Icon,
    "van-radio-group": RadioGroup,
    "van-radio": Radio,
    "van-checkbox": Checkbox,
    "van-area": Area,
    "van-popup": Popup,
    vanNavBar: NavBar
  },
  data() {
    return {
      checked: false,
      address: [],
      second: [],
      third: [],
      provinceValue: -1,
      cityValue: -1,
      districtValue: -1,
      data: {
        famale: 1,
        city: "",
        name: "",
        country: 0,
        district: "",
        is_default: 0,
        mobile: "",
        province: "",
        twon: ""
      },
      is_default: 1,
      address: [],
      site: util.site,
      show: false,
      changed: false
    };
  },
  created() {
    this.getAddressData();
    /*     if (this.$route.query.data.province == null) {
      this.$route.query.data.province = "";
    }
    this.data = {
      address_id: this.$route.query.data.address_id,
      name: this.$route.query.data.consignee,
      province: this.$route.query.data.province,
      city: this.$route.query.data.city,
      district: this.$route.query.data.district,
      address: this.$route.query.data.address,
      mobile: this.$route.query.data.mobile,
      is_default: this.$route.query.data.is_default,
      famale: String(this.$route.query.data.famale)
    }; */
  },
  watch: {
    data() {
      this.changed = true;
    }
  },
  methods: {
    default_() {
      this.data.is_default = this.data.is_default == 1 ? 0 : 1;
    },
    /*  getData() {
      this.$get("user/edit_address", { address_id: this.$route.query.id }).then(
        res => {
          this.data = {
            address_id: this.$route.query.id,
            name: res.data.consignee,
            province: res.data.province,
            city: res.data.city,
            district: res.data.district,
            address: res.data.address,
            mobile: res.data.mobile,
            famale: "1"
          };
          this.is_default = res.data.is_default ? true : false;
        }
      );
    }, */
    // 
    changeAddress(val) {
      this.data.province = val[0].name;
      this.data.city = val[1].name;
      this.data.district = val[2].name;
      this.data.provinceValue = val[0].code;
      this.data.cityValue = val[1].code;
      this.data.districtValue = val[2].code;
      this.show = false;
    },
    // 
    submit() {
      for (let i in this.data) {
        if (this.data[i] == "") {
          Toast("");
          return false;
        }
      }
      this.data.is_default = this.is_default ? "1" : "0";
      if (this.$route.query.id) {
        this.$post("api/user/edit_address", this.data).then(res => {
          Toast("");
          this.$router.go(-1);
        });
      } else {
        this.$post("api/user/receiving_address", this.data).then(res => {
          Toast("");
          this.$router.go(-1);
        });
      }
    },
    sub() {
      if (!this.data.province && !this.data.mobile && !this.data.consignee) {
        Toast("");
      } else if (!/^1[3456789]\d{9}$/.test(this.data.mobile)) {
        Toast("");
      } else {
        if (this.$route.query.data && this.changed) {
          edit_address(this.data).then(res => {
            Toast("");
            this.$router.go(-1);
          });
        } else if (!this.$route.query.data) {
          let params = new URLSearchParams();
          params.append("openid", localStorage.getItem("openid"));
          params.append("consignee", this.data.name);
          params.append("mobile", this.data.mobile);
          params.append("province", String(this.data.province));
          params.append("city", String(this.data.city));
          params.append("district", String(this.data.district));
          params.append("sex", "1");
          params.append("address", this.data.address);
          params.append("is_default", "1");
          axios.post(url.addAddress, params).then(res => {
            Toast("");
            this.$router.go(-1);
          });
        } else {
          Toast("");
          this.$router.go(-1);
        }
      }
    }
  }
};
</script>

<style scoped lang="scss">
.wrap {
  min-height: 100vh;
  background: #fff;
  padding: 0 10px;
}
.item {
  padding: 15px;
  border-bottom: 1px solid #e1e1e1;
  font-size: 16px;
  display: flex;
  .flex {
    display: flex;
    width: 100%;
    justify-content: center;
  }
  input {
    flex: 1;
    border: none;
    outline: none;
  }
  .radio {
    margin-left: 50px;
  }
  .flex_1 {
    span {
      color: #000 !important;
    }
  }
}
.checkbox_wrap {
  margin: 30px 10px;
  font-size: 36px;
}
.button {
  margin: 30px auto;
  background: linear-gradient(to right, #ff462d, #ff9e3f);
  color: #fff;
  border-radius: 60px;
  font-size: 16px;
  padding: 15px;
  text-align: center;
}
.flex1 {
  display: flex;
}
input::-webkit-input-placeholder {
  color: #999999;
}
#dizhi {
  display: flex;
  align-items: center;
  font-size: 16px;
  color: #323233;
  margin-top: 10px;
  .radio {
    display: inline-block;
    // vertical-align: middle;
    height: 13pt;
    width: 13pt;
    border: 2px solid #323233;
    border-radius: 50%;
    margin-right: 10pt;
  }
  .active {
    background: url() no-repeat;
    background-size: 100%;
    border-color: #fff !important;
  }
}
</style>



